import React from 'react';
import {
    Box,
    IconButton,
    Tooltip,
    Select,
    MenuItem,
    Card,
    CardContent,
    Dialog,
    DialogActions,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import SVG from 'react-inlinesvg';
import {
    Check as CheckIcon,
    Refresh as RefreshIcon,
    GetApp as DownloadIcon,
    Visibility as VisibilityIcon,
} from '@material-ui/icons';
import Highlight from 'react-highlight';
import { useTranslation } from 'react-i18next';
import _ from 'lodash';
import clsx from 'clsx';
import * as d3 from 'd3';
import JSZip from 'jszip';
import FileSaver from 'file-saver';
import Button from '../Button';
import Confirmation from '../Confirmation';
import FileUploader from '../FileUploader';
import {
    useStore,
    useActions,
} from '../../hooks';

export const useStyles = makeStyles(theme => ({
    root: {
        height: '100%',
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 10,
    },
    jsonViewer: {
        position: 'relative',
        border: '1px solid black',
        height: '100%',
        width: '100%',
        margin: 10,
        minWidth: 450,
    },
    svgViewer: {
        position: 'relative',
        border: '1px solid black',
        height: '100%',
        width: '100%',
        margin: 10,
        minWidth: 450,
    },
    loadButton: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        margin: 10,
    },
    svgButtonTopButtonContainer: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    picklist: {
        margin: 10,
        minWidth: 150,
    },
    assignButton: { margin: 10 },
    cards: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        alignContent: 'flex-start',
        height: '90%',
        overflow: 'auto',
    },
    card: {
        minWidth: 180,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        margin: 10,
        cursor: 'pointer',
        border: '1px solid white',
    },
    selectedCards: { border: '1px solid tomato' },
    svgContainer: {
        background: '#ffffff',
        marginTop: theme.spacing(5),
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        padding: theme.spacing(2),
    },
    svg: {
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        overflow: 'hidden',
    },
    jsonView: { padding: 10 },
    cardContainer: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
    },
    button: { textTransform: 'none' },
    svgPopup: {
        background: '#ffffff',
        padding: 10,
    },
}));

function Viewer() {
    const classes = useStyles();
    const [t] = useTranslation('common');
    const {
        jsonContent,
        svgContent,
        picklistValue,
        picklistOptions,
        selectedCards,
        selectedSvgElements,
    } = useStore('tool');
    const {
        setJsonContent,
        setSvgContent,
        setPicklistValue,
        setPicklistOptions,
        setSelectedCards,
        setSelectedSvgElements,
        reset,
    } = useActions('tool');
    const [openConfirmation, setOpenConfirmation] = React.useState(false);
    const [openJsonPreview, setOpenJsonPreview] = React.useState(false);
    const [openCardToSvgConnectionPreview, setOpenCardToSvgConnectionPreview] = React.useState(false);
    const [selectedPopup, setSelectedPopup] = React.useState({});
    const [popupStrokeMap, setPopupStrokeMap] = React.useState({});

    const selectedCardsRef = React.useRef(null);
    selectedCardsRef.current = selectedCards;

    const selectedSvgElementsRef = React.useRef(null);
    selectedSvgElementsRef.current = selectedSvgElements;

    const popupStrokeMapRef = React.useRef(null);
    popupStrokeMapRef.current = popupStrokeMap;

    const selectedPopupRef = React.useRef(null);
    selectedPopupRef.current = selectedPopup;

    React.useEffect(() => {
        if (!jsonContent && _.isEmpty(selectedCards)) return;
        setTimeout(() => {
            d3.select('#workspace-svg')
                .select('g')
                .selectAll('*')
                .each(function(_, index) {
                    d3.select(this).attr('id', `e-${index}`);
                })
                .on('click', function() {
                    if (Object.keys(selectedCardsRef.current).length > 0) {
                        const { id } = this;
                        if (id in selectedSvgElementsRef.current) {
                            const selections = { ...selectedSvgElementsRef.current };
                            d3.select(this)
                                .attr('stroke', selections[id].stroke);
                            delete selections[id];
                            setSelectedSvgElements(selections);
                        } else {
                            const selections = { ...selectedSvgElementsRef.current };
                            const elem = d3.select(this);
                            selections[id] = {
                                id,
                                stroke: elem.attr('stroke'),
                            };
                            setSelectedSvgElements(selections);
                            elem.attr('stroke', 'tomato');
                        }
                    }
                })
                .attr('pointer-events', 'fill')
                .style('cursor', 'pointer');
        }, 0);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [svgContent, jsonContent]);

    const readFileAsUrl = file => new Promise(resolve => {
        const fileReader = new FileReader();
        fileReader.onload = () => resolve(fileReader.result);
        fileReader.readAsDataURL(file);
    });

    const clearSelections = () => {
        setSelectedCards({});
        _.each(selectedSvgElements, (elementProps, id) => {
            const elem = d3.select(`#${id}`);
            elem.attr('stroke', elementProps.stroke);
        });
        setSelectedSvgElements({});
    };

    const readFileAsJson = async file => {
        const text = await file.text();
        return JSON.parse(text);
    };

    const onJsonFileUpload = async files => {
        const file = files[0];
        const jsonFile = await readFileAsJson(file);
        const options = Object.keys(jsonFile);
        setPicklistOptions(options);
        setJsonContent(jsonFile);
    };

    const onSvgFileUpload = async files => {
        const file = files[0];
        const url = await readFileAsUrl(file);
        setSvgContent(url);
    };

    const onPicklistChange = event => {
        setPicklistValue(event.target.value);
        clearSelections();
    };

    const onCardClick = (index, key) => {
        const selections = { ...selectedCardsRef.current };
        if (index in selections) {
            delete selections[index];
        } else {
            selections[index] = {
                key,
                index,
            };
        }
        setSelectedCards(selections);
        if (_.isEmpty(selections)) {
            _.each(selectedSvgElementsRef.current, (elementProps, id) => {
                d3.select(`#${id}`)
                    .attr('stroke', elementProps.stroke);
            });
            setSelectedSvgElements({});
        }
    };

    const onSeeConnection = (index, key) => {
        setOpenCardToSvgConnectionPreview(true);
        setSelectedPopup({ index, key });
        const data = index === -1 ? jsonContent[key] : jsonContent[key][index];
        if (data.svg == null || !Array.isArray(data.svg) || data.svg.length === 0) return;
        const svgElementIdSet = new Set(data.svg);
        const strokeMap = {};
        setTimeout(() => {
            d3.select('#popup-svg')
                .select('g')
                .selectAll('*')
                .each(function(_, index) {
                    const elem = d3.select(this);
                    const id = `e-${index}`;
                    if (svgElementIdSet.has(id)) {
                        const currentStroke = elem.attr('stroke');
                        strokeMap[index] = currentStroke;
                        elem.attr('stroke', 'tomato');
                    }
                });
        }, 0);
        setPopupStrokeMap(strokeMap);
    };

    const onRemoveConnection = () => {
        const { index, key } = selectedPopupRef.current;
        const strokeMap = popupStrokeMapRef.current;
        const clonedJsonContent = _.cloneDeep(jsonContent);
        const data = index === -1 ? clonedJsonContent[key] : clonedJsonContent[key][index];
        if (data.svg == null || !Array.isArray(data.svg) || data.svg.length === 0) return;
        data.svg = [];
        setTimeout(() => {
            d3.select('#popup-svg')
                .select('g')
                .selectAll('*')
                .each(function(_, index) {
                    const elem = d3.select(this);
                    if (index in strokeMap) {
                        elem.attr('stroke', strokeMap[index]);
                    }
                });
        }, 0);
        setJsonContent(clonedJsonContent);
    };

    const onRefresh = () => {
        setOpenConfirmation(true);
    };

    const onRefreshConfirm = () => {
        setOpenConfirmation(false);
        reset();
    };

    const onAssign = async () => {
        if (_.isEmpty(selectedCardsRef.current) || _.isEmpty(selectedSvgElementsRef.current)) return;
        await new Promise(resolve => {
            const clonedJsonContent = _.cloneDeep(jsonContent);
            _.each(selectedCardsRef.current, value => {
                const { key, index } = value;
                if (index === -1) {
                    clonedJsonContent[key].svg = _.map(selectedSvgElementsRef.current, value => value.id);
                } else {
                    clonedJsonContent[key][index].svg = _.map(selectedSvgElementsRef.current, value => value.id);
                }
            });
            setJsonContent(clonedJsonContent);
            clearSelections();
            setTimeout(resolve, 200);
        });
    };

    const getCardContent = data => _.reduce(data, (accumulator, value, key) => {
        if (Array.isArray(value)) return accumulator;
        accumulator.push(
            <Box
                key={key}
                p={0.5}
            >
                <b>{key}</b>
                {`: ${value}`}
            </Box>,
        );
        return accumulator;
    }, []);

    const showJson = () => {
        setOpenJsonPreview(true);
    };

    const onDownload = () => {
        const elem = document.getElementById('workspace-svg-container');
        const svgEl = elem.children[0];
        svgEl.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
        const svgData = svgEl.outerHTML;
        const preface = '<?xml version="1.0" standalone="no"?>\r\n';
        const svgBlob = new Blob([preface, svgData], { type: 'image/svg+xml;charset=utf-8' });

        const zip = JSZip();
        zip.file('result.svg', svgBlob);
        zip.file('result.json', JSON.stringify(jsonContent, null, 2));
        zip.generateAsync({ type: 'blob' })
            .then(content => {
                FileSaver.saveAs(content, 'result.zip');
            });
    };

    return (
        <>
            <Box className={classes.root}>
                <Box className={classes.jsonViewer}>
                    <Box>
                        <Select
                            className={classes.picklist}
                            defaultValue={picklistValue}
                            disabled={!(picklistOptions.length > 0)}
                            onChange={onPicklistChange}
                        >
                            {picklistOptions.map((option, index) => (
                                <MenuItem
                                    key={index}
                                    value={option}
                                >
                                    {option}
                                </MenuItem>
                            ))}
                        </Select>
                        <Tooltip title={t('jsonViewer.showJson')}>
                            <IconButton
                                disabled={!jsonContent}
                                onClick={showJson}
                            >
                                <VisibilityIcon />
                            </IconButton>
                        </Tooltip>
                    </Box>
                    {picklistValue && (
                        <Box className={classes.cards}>
                            {!Array.isArray(jsonContent[picklistValue]) && (
                                <Box className={classes.cardContainer}>
                                    <Card
                                        className={clsx(classes.card, { [classes.selectedCards]: selectedCards[-1] })}
                                        onClick={() => onCardClick(-1, picklistValue)}
                                    >
                                        <CardContent>
                                            {getCardContent(jsonContent[picklistValue])}
                                        </CardContent>
                                    </Card>
                                    <Button
                                        className={classes.button}
                                        color='primary'
                                        onClick={() => onSeeConnection(-1, picklistValue)}
                                        size='small'
                                    >
                                        {t('card.connections')}
                                    </Button>
                                </Box>
                            )}
                            {Array.isArray(jsonContent[picklistValue]) && jsonContent[picklistValue].map((value, index) => (
                                <Box
                                    key={index}
                                    className={classes.cardContainer}
                                >
                                    <Card
                                        className={clsx(classes.card, { [classes.selectedCards]: selectedCards[index] })}
                                        onClick={() => onCardClick(index, picklistValue)}
                                    >
                                        <CardContent>
                                            {getCardContent(value)}
                                        </CardContent>
                                    </Card>
                                    <Button
                                        className={classes.button}
                                        color='primary'
                                        onClick={() => onSeeConnection(index, picklistValue)}
                                        size='small'
                                    >
                                        {t('card.connections')}
                                    </Button>
                                </Box>
                            ))}
                        </Box>
                    )}
                    <FileUploader
                        acceptedFiles={['application/json']}
                        className={classes.loadButton}
                        onSave={onJsonFileUpload}
                        title={t('jsonViewer.loadJson')}
                    />
                </Box>
                <Box className={classes.svgViewer}>
                    <Box className={classes.svgButtonTopButtonContainer}>
                        <Button
                            className={classes.assignButton}
                            color='primary'
                            disabled={_.isEmpty(selectedCardsRef.current) || _.isEmpty(selectedSvgElementsRef.current)}
                            endIcon={<CheckIcon />}
                            onClick={onAssign}
                            variant='contained'
                        >
                            {t('svgViewer.assign')}
                        </Button>
                        <Box>
                            <Tooltip title={t('svgViewer.reset')}>
                                <IconButton
                                    disabled={!jsonContent && !svgContent}
                                    onClick={onRefresh}
                                >
                                    <RefreshIcon />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={t('svgViewer.download')}>
                                <IconButton
                                    disabled={!jsonContent || !svgContent}
                                    onClick={onDownload}
                                >
                                    <DownloadIcon />
                                </IconButton>
                            </Tooltip>
                        </Box>
                    </Box>
                    {svgContent && (
                        <Box
                            className={classes.svgContainer}
                            id='workspace-svg-container'
                        >
                            <SVG
                                className={classes.svg}
                                id='workspace-svg'
                                src={svgContent}
                            />
                        </Box>
                    )}
                    <FileUploader
                        acceptedFiles={['image/svg+xml']}
                        className={classes.loadButton}
                        onSave={onSvgFileUpload}
                        title={t('svgViewer.loadSvg')}
                    />
                </Box>
            </Box>
            <Confirmation
                content={t('confirmation.resetWarning')}
                onClose={() => setOpenConfirmation(false)}
                onConfirm={onRefreshConfirm}
                open={openConfirmation}
            />
            <Dialog
                fullScreen
                onClose={() => setOpenJsonPreview(false)}
                open={openJsonPreview}
            >
                <Box
                    pl={1}
                    pr={1}
                >
                    <Highlight className={clsx('json', classes.jsonView)}>
                        {JSON.stringify(jsonContent, null, 2)}
                    </Highlight>
                </Box>
            </Dialog>
            {!_.isEmpty(selectedPopup)
                && (
                    <Dialog
                        onClose={() => setOpenCardToSvgConnectionPreview(false)}
                        open={openCardToSvgConnectionPreview}
                    >
                        <Box className={classes.svgPopup}>
                            <SVG
                                className={classes.svg}
                                id='popup-svg'
                                src={svgContent}
                            />
                        </Box>
                        <DialogActions className={classes.svgPopup}>
                            <Button
                                autoFocus
                                className={classes.button}
                                color='primary'
                                disabled={
                                    _.isEmpty((selectedPopup.index === -1
                                        ? jsonContent[selectedPopup.key]
                                        : jsonContent[selectedPopup.key][selectedPopup.index]).svg)
                                }
                                inline
                                onClick={onRemoveConnection}
                                size='small'
                            >
                                {t('svgPopup.removeConnections')}
                            </Button>
                        </DialogActions>
                    </Dialog>
                )}
        </>
    );
}

Viewer.propTypes = {};

Viewer.defaultProps = {};

export default Viewer;
