import { createSlice } from '@reduxjs/toolkit';
import { reset } from '../actions';

const initialState = {
    jsonContent: '',
    svgContent: '',
    picklistValue: '',
    picklistOptions: [],
    selecedCards: {},
    selectedSvgElements: {},
};

export default createSlice({
    name: 'tool',
    initialState,
    reducers: {
        setJsonContent: (state, action) => {
            state.jsonContent = action.payload;
        },
        setSvgContent: (state, action) => {
            state.svgContent = action.payload;
        },
        setPicklistValue: (state, action) => {
            state.picklistValue = action.payload;
        },
        setPicklistOptions: (state, action) => {
            state.picklistOptions = action.payload;
        },
        setSelectedCards: (state, action) => {
            state.selectedCards = action.payload;
        },
        setSelectedSvgElements: (state, action) => {
            state.selectedSvgElements = action.payload;
        },
    },
    extraReducers: {
        [reset]: state => {
            Object.assign(state, initialState);
        },
    },
});
