import React from 'react';
import PropTypes from 'prop-types';
import { Box, makeStyles, Typography } from '@material-ui/core';
import { DropzoneDialog } from 'material-ui-dropzone';
import { useTranslation } from 'react-i18next';
import Button from '../Button';

export const useStyles = makeStyles(theme => ({ error: { color: theme.palette.error.main } }));

const FileUploader = ({
    className,
    style,
    onSave,
    error,
    acceptedFiles,
    title,
}) => {
    const classes = useStyles();
    const [filename, setFilename] = React.useState('');
    const [open, setOpen] = React.useState(false);
    const [t] = useTranslation('common');

    const handleClose = () => {
        setOpen(false);
    };

    const handleSave = files => {
        setOpen(false);
        setFilename(files[0].name);
        onSave(files);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    return (
        <Box
            alignItems='center'
            className={className}
            display='flex'
            flexDirection='row'
            style={style}
        >
            <Button
                color='primary'
                onClick={handleOpen}
            >
                {filename === '' ? title || t('fileUploader.chooseFile') : t('fileUploader.changeFile')}
            </Button>
            <DropzoneDialog
                acceptedFiles={acceptedFiles}
                filesLimit={1}
                maxFileSize={Infinity}
                onClose={handleClose}
                onSave={handleSave}
                open={open}
                showPreviews
            />
            {error && <Typography className={classes.error}>{error.message}</Typography>}
        </Box>
    );
};

FileUploader.propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    onSave: PropTypes.func.isRequired,
    error: PropTypes.oneOfType([
        PropTypes.object,
        PropTypes.bool,
    ]),
    acceptedFiles: PropTypes.array,
    title: PropTypes.string,
};

FileUploader.defaultProps = {
    className: '',
    style: {},
    error: undefined,
    acceptedFiles: [],
    title: undefined,
};

export default FileUploader;
